import { Subject, BehaviorSubject, Observable } from 'rxjs/Rx';
import { Injectable } from '@angular/core';

@Injectable()
export class BorrowerService {
    private _borrower = new Subject();
    borrower$ = this._borrower.asObservable();

    borrowerChange(borrower) {
        this._borrower.next(borrower);
    }
}
export class IncomeService {
    private _income = new Subject();
    incomes$ = this._income.asObservable();

    incomeChange(income) {
        this._income.next(income);
    }
}
export class DebtService {
    private _debt = new Subject();
    debts$ = this._debt.asObservable();

    debtChange(debt) {
        this._debt.next(debt);
    }
}

export class LoanOptionService {
    private _loanOption = new Subject();
    loanOption$ = this._loanOption.asObservable();

    loanOptionChange(loanOption) {
        this._loanOption.next(loanOption);
    }
}

// @Injectable()
export class LoanSaveService {
    private _loan = new BehaviorSubject(null);
    loans$ = this._loan.asObservable();

    loanChange(loan) {
        this._loan.next(loan);
    }
}


@Injectable()
export class MessageService {
    private subject = new Subject<any>();

    sendMessage(message: string) {
        this.subject.next({ text: message });
    }

    clearMessage() {
        this.subject.next();
    }

    getMessage(): Observable<any> {
        return this.subject.asObservable();
    }
}


