import { EventEmitter, Inject, Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

// import {APP_CONFIG} from '../../config/app.config';
// import {IAppConfig} from '../../config/iapp.config';

import { Borrower, Loan, ILoan, IShareLoan, ShareLoanModel } from './models/index';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../../../environments/environment';
import { ApiService } from '../../../shared/services/index';


@Injectable()
export class LoanService {
  request$: EventEmitter<any>;

  private headers;
  private borroweresUrl;
  private translations: Array<string>;


  private handleError(error: any): Promise<any> {
    this.request$.emit('finished');
    return Promise.reject(error.message || error);
  }

  constructor(
    private http: Http,
    private apiService: ApiService,
  ) {
    this.request$ = new EventEmitter();
    this.headers = new Headers({ 'Content-Type': 'application/json' });
  }
  private setHeaders(): Headers {
    const headersConfig = {
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    };

    // if (this.jwtService.getToken()) {
    //   headersConfig['Authorization'] = `Token ${this.jwtService.getToken()}`;
    // }
    return new Headers(headersConfig);
  }
  private formatErrors(error: any) {
    return Observable.throw(error.json());
  }
  getById(loanId): Observable<Loan> {
    this.request$.emit('starting');
    return this.apiService.get('/loan/get' + '/' + loanId)
      .map(response => {
        this.request$.emit('finished');
        console.log('/loan/get' + '/' + loanId, response);
        return response.data as Loan;
      })
      .catch(this.handleError);
  }
  create(): Observable<Loan> {
    this.request$.emit('starting');
    return this.apiService.post('/loan/new')
      .map(response => {
        this.request$.emit('finished');
        return response as Loan;
      })
      .catch(this.handleError);
  }

  save(loan: ILoan): Observable<Loan> {
    this.request$.emit('starting');
    return this.apiService.post('/loan/save', loan)
      .map(response => {
        this.request$.emit('finished');
        return response as Loan;
      })
      .catch(this.handleError);
  }
  shareLoan(loan: IShareLoan): Observable<ShareLoanModel> {
    this.request$.emit('starting');
    return this.apiService.post('/loan/share', loan)
      .map(response => {
        this.request$.emit('finished');
        return response as ShareLoanModel;
      })
      .catch(this.handleError);
  }
}



