import { Component, OnInit, ViewChild } from '@angular/core';
const screenfull = require('screenfull');
const browser = require('jquery.browser');
declare var $: any;

import { UserblockService } from '../sidebar/userblock/userblock.service';
import { SettingsService } from '../../core/settings/settings.service';
import { MenuService } from '../../core/menu/menu.service';
import { LoanService } from "app/routes/loan/shared/loan.service";
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { LoanSaveService, MessageService } from "app/routes/loan/shared/borrower.service";

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

    navCollapsed = true; // for horizontal layout
    menuItems = []; // for horizontal layout

    isNavSearchVisible: boolean;
    @ViewChild('fsbutton') fsbutton;  // the fullscreen button
    isLoanPage: boolean;

    constructor(public menu: MenuService, public userblockService: UserblockService,
        public settings: SettingsService,
        private router: Router,
        private route: ActivatedRoute,
        public loanService: LoanService,
        private loanSaveService: LoanSaveService,
        private messageService: MessageService
    ) {

        // show only a few items on demo
        this.menuItems = menu.getMenu().slice(0, 4); // for horizontal layout
        // console.log('sdd', this.router);
        const re = new RegExp("^/loan-calculator/[0-9]*$");
        const re1 = new RegExp("^/loan-calculator/customer-view/[0-9]*$");
        this.router.events.subscribe((event: any) => {
            if (re.test(event.url) || re1.test(event.url)) {
                this.isLoanPage = true;
            }
            else {
                this.isLoanPage = false;
            }
        });
    }
    saveLoan() {
        this.messageService.sendMessage('Save Loan');
        // this.loanSaveService.loanChange('loan save');
    }

    ngOnInit() {
        this.isNavSearchVisible = false;
        if (browser.msie) { // Not supported under IE
            this.fsbutton.nativeElement.style.display = 'none';
        }
    }

    toggleUserBlock(event) {
        event.preventDefault();
        this.userblockService.toggleVisibility();
    }

    openNavSearch(event) {
        event.preventDefault();
        event.stopPropagation();
        this.setNavSearchVisible(true);
    }

    setNavSearchVisible(stat: boolean) {
        // console.log(stat);
        this.isNavSearchVisible = stat;
    }

    getNavSearchVisible() {
        return this.isNavSearchVisible;
    }

    toggleOffsidebar() {
        this.settings.layout.offsidebarOpen = !this.settings.layout.offsidebarOpen;
    }

    toggleCollapsedSideabar() {
        this.settings.layout.isCollapsed = !this.settings.layout.isCollapsed;
    }

    isCollapsedText() {
        return this.settings.layout.isCollapsedText;
    }

    toggleFullScreen(event) {

        if (screenfull.enabled) {
            screenfull.toggle();
        }
        // Switch icon indicator
        let el = $(this.fsbutton.nativeElement);
        if (screenfull.isFullscreen) {
            el.children('em').removeClass('fa-expand').addClass('fa-compress');
        }
        else {
            el.children('em').removeClass('fa-compress').addClass('fa-expand');
        }
    }

    createNewLoan() {
        // this.isSubmitting = true;

        // // update the model
        // this.updateArticle(this.articleForm.value);

        // post the changes
        this.loanService
            .create()
            .subscribe(
            loan => this.router.navigateByUrl('/loan-calculator/' + loan.loanId),
            err => {
                // this.errors = err;
                // this.isSubmitting = false;
            }
            );
    }
}



